$black: #212121;
$dark_gray: #202020;
$white: #ffffff;
$main-color: #1eacc7;
$second-color: #ff5656;

@mixin btn_hover {
	&:hover {
		background-image: none;
		background-color: $main-color; } }

* {
	font-family: 'Roboto', sans-serif; }

header {
	position: relative;
	height: 50px; }

.container {
	display: block;
	position: relative;
	margin: 0 auto; }

nav {
	position: fixed;
	width: 100%;
	height: 50px;
	background-color: $black;
	z-index: 5; }

.menu {
	display: flex;
	justify-content: space-between;
	padding: 13px 70px;
	margin: 0;
	list-style-type: none;
	&_link {
		color: $white;
		font-size: 15px;
		font-weight: 300; }
	&_item {
		border-right: 2px dotted black;
		padding-right: 28px; } }

.subheader {
	position: absolute;
	top: 50px;
	width: 100%;
	height: 84px;
	background-color: rgba(0, 0, 0, 0.6);
	&_logo {
		display: block;
		max-width: 100%;
		margin-top: 16px; }
	&_official {
		margin-top: 6px;
		opacity: 0.5;
		color: $white;
		font-family: Roboto;
		font-size: 13px;
		font-weight: 400; }
	&_call {
		color: $white;
		font-size: 12px;
		margin-top: 19px;
		font-weight: 300;
		text-align: right; }
	&_phone {
		display: block;
		margin-top: 8px;
		color: $white;
		font-size: 22px;
		font-weight: 400;
		text-transform: uppercase;
		text-align: right;
		line-height: 12px; }
	&_btn {
		margin-top: 19px;
		width: 172px;
		height: 46px;
		box-shadow: inset 0 -3px 0 rgba(0, 0, 0, 0.12);
		border: none;
		border-radius: 4px;
		background-image: linear-gradient(0deg, #1eacc7 0%, #4ce2ff 100%);
		text-shadow: 1px 1px 0px rgba(0, 0, 0, 0.21);
		color: $white;
		font-size: 14px;
		font-weight: 400;
		text-transform: uppercase;
		@include btn_hover; } }

.promo {
	min-height: 800px;
	background: url('../img/bg/uber_bg.png') center (center / cover) no-repeat;
	padding: 150px 0 135px 0;
	&_header {
		color: $white;
		font-weight: 700;
		text-transform: uppercase;
		font-size: 60px;
		line-height: 60px;
		margin: 0;
		text-align: center; }
	&_subheader {
		color: $white;
		text-transform: uppercase;
		font-weight: 700;
		font-size: 36px;
		line-height: 36px;
		margin: 0;
		margin-top: 22px;
		text-align: center; }
	&_descr {
		color: $white;
		font-size: 18px;
		font-weight: 400;
		line-height: 24px;
		text-align: center;
		margin-top: 22px; }
	&_btn {
		display: block;
		margin: 118px auto 0 auto;
		width: 259px;
		height: 63px;
		box-shadow: inset 0 -4px 0 rgba(0, 0, 0, 0.12);
		border: none;
		border-radius: 4px;
		background-image: linear-gradient(to top, $main-color 0%, #4ce2ff 100%);
		text-shadow: 1px 1px 0 rgba(0, 0, 0, 0.21);
		color: $white;
		font-size: 18px;
		font-weight: 400;
		text-transform: uppercase;
		@include btn_hover; } }

.reasons {
	padding: 71px 0 65px 0; }

.label {
	position: relative;
	margin: 0 auto;
	height: 24px;
	width: 112px;
	padding: 5px 12px;
	background-color: #000000;
	color: #ffffff;
	font-size: 11px;
	font-weight: 300;
	text-transform: uppercase;
	text-align: center;
	z-index: 2;
	&_white {
		background-color: #fff;
		color: #000; }
	&_min {
		width: 76px; } }

.title {
	position: relative;
	margin-bottom: 0;
	margin-top: 38px;
	color: #222222;
	font-size: 38px;
	line-height: 38px;
	font-weight: 700;
	text-transform: uppercase;
	text-align: center;
	z-index: 2;
	&_white {
		color: #fff; } }

.subtitle {
	position: relative;
	margin-top: 19px;
	color: #222222;
	font-size: 17px;
	font-weight: 400;
	text-align: center;
	z-index: 2;
	&_white {
		color: #fff; } }

.reasons .reasons_block {
	display: flex;
	justify-content: center;
	align-items: center;
	margin-top: 35px;
	.reasons_round {
		display: flex;
		justify-content: center;
		align-items: center;
		margin-right: 26px;
		width: 113px;
		height: 113px;
		background-color: $main-color;
		border-radius: 100%; }
	.reasons_descr {
		width: 343px;
		.reasons_subtitle {
			line-height: 21px;
			color: $main-color;
			font-size: 17px;
			font-weight: 400; }
		.reasons_text {
			color: #202020;
			font-size: 15px;
			font-weight: 300;
			line-height: 21px; } } }

.mobile {
	min-height: 864px;
	padding: 48px 0 79px 0;
	background: url('../img/bg/mobile_app.png') center (center / cover) no-repeat;
	.row {
		margin-top: 32px; }
	&_item {
		position: relative;
		width: 320px;
		height: 280px;
		margin: 0 auto;
		margin-bottom: 19px;
		padding-top: 92px;
		&_1 {
			background: url('../img/bg/mobile_screen_1.jpg') center (center / cover) no-repeat; }
		&_2 {
			background: url('../img/bg/mobile_screen_2.jpg') center (center / cover) no-repeat; }
		&_3 {
			background: url('../img/bg/mobile_screen_3.jpg') center (center / cover) no-repeat; }
		&_4 {
			background: url('../img/bg/mobile_screen_4.jpg') center (center / cover) no-repeat; }
		&_5 {
			background: url('../img/bg/mobile_screen_5.jpg') center (center / cover) no-repeat; }
		&_6 {
			background: url('../img/bg/mobile_screen_6.jpg') center (center / cover) no-repeat; }
		&_subtitle {
			color: #ffffff;
			font-size: 24px;
			font-weight: 400;
			line-height: 31px;
			text-transform: uppercase;
			text-align: center; }
		&_plus {
			display: flex;
			justify-content: center;
			align-items: center;
			position: absolute;
			bottom: 35px;
			left: 50%;
			transform: translateX(-50%);
			width: 26px;
			height: 26px;
			background-color: #000000;
			margin: 0 auto;
			border-radius: 100%;
			cursor: pointer;
			&:before {
				content: '';
				display: block;
				width: 14px;
				height: 2px;
				background-color: $main-color; }
			&:after {
				content: '';
				position: absolute;
				left: 50%;
				transform: translate(-50%);
				display: block;
				width: 2px;
				height: 14px;
				background-color: $main-color;
				transition: 0.3s all; }
			&:hover {
				&:before, &:after {
					background-color: $second-color; } } } } }

.car-section {
	.container {
		.dark-top {
			padding: 40px 0 0 0;
			margin: 0 auto;
			min-height: 503px;
			position: relative;
			width: 100vw;
			left: 50%;
			transform: translateX(-50%);
			background-color: #070716;
			justify-content: center;
			.white-label {
				display: block;
				justify-content: center;
				margin: 0 auto;
				width: 135px;
				height: 27px;
				background-color: $white;
				color: $black;
				font-size: 17px;
				font-weight: 400;
				text-transform: uppercase;
				text-align: center; }
			.white-title {
				display: block;
				color: $white;
				text-align: center;
				padding: 32px 0 0 0;
				font-size: 38px;
				font-weight: 700;
				line-height: 48px;
				text-transform: uppercase; }
			.white-text {
				display: block;
				color: $white;
				text-align: center;
				font-size: 17px;
				font-weight: 400;
				line-height: 21px; } }
		.picture {
			display: block;
			align-items: center;
			margin: -153px auto 150px auto;
			transform: translateY(-25%);
			z-index: 3; }
		.light-bottom {
			display: block;
			min-height: 235px;
			width: 100%;
			position: relative;
			width: 100vw;
			left: 50%;
			transform: translateX(-50%);
			background: url('../img/bg/mobile_app.png') center (center / cover) no-repeat;
			margin: -499px auto 0 auto;
			padding: 57px 0 138px 0;
			z-index: -1;
			.light-bottom-text-top {
				display: block;
				margin: 237px auto 0 auto;
				padding-top: 100px;
				text-align: center;
				font-size: 23px;
				margin: 0 auto;
				justify-content: center; }
			.light-bottom-text-bottom {
				display: block;
				text-align: center;
				font-size: 23px;
				margin: -23px auto;
				justify-content: center; } } } }

.require {
	padding: 68px 0 84px 0;
	&_block {
		margin: 33px 0;
		&_nmb {
			margin-bottom: 0px; } }
	&_title {
		color: #1ca8c3;
		font-size: 22px;
		font-weight: 400; }
	&_list {
		list-style-type: none;
		padding-left: 50px;
		margin-top: 22px;
		li {
			position: relative;
			font-size: 17px;
			font-weight: 300;
			line-height: 24px;
			margin-bottom: 10px;
			&:before {
				content: '';
				display: block;
				position: absolute;
				left: -32px;
				top: 50%;
				transform: translateY(-50%);
				width: 21px;
				height: 20px;
				background: url('../icons/check_blue.png') center (center / cover) no-repeat; } } }
	&_descr {
		margin-top: 25px;
		font-size: 17px;
		font-weight: 300;
		line-height: 24px;
		color: #1ca8c3; }
	.warning {
		.require_title {
			color: $second-color; }
		.require_list {
			li {
				&:before {
					background: url('../icons/check_red.png') center (center / cover) no-repeat; } } } } }

.world_wide {
	display: block;
	min-height: 800px;
	background: url('../img/bg/world_bg.png') center (center / cover) no-repeat;
	position: relative;
	.top-text {
		padding: 40px 0 0 0;
		.white-label {
			display: block;
			justify-content: center;
			margin: 0 auto;
			width: 135px;
			height: 27px;
			background-color: $white;
			color: $black;
			font-size: 17px;
			font-weight: 400;
			text-transform: uppercase;
			text-align: center; }
		.white-title {
			display: block;
			color: $white;
			text-align: center;
			padding: 32px 0 0 0;
			font-size: 38px;
			font-weight: 700;
			line-height: 48px;
			text-transform: uppercase; }
		.white-text {
			display: block;
			color: $white;
			text-align: center;
			font-size: 17px;
			font-weight: 400;
			line-height: 21px; } }
	.bottom-text {
		display: flex;
		justify-content: center;
		align-items: center;
		position: absolute;
		bottom: 23px;
		width: 100%;
		.blue-label {
			color: $main-color;
			font-size: 24px;
			font-weight: 200;
			line-height: 68px;
			letter-spacing: 0px;
			text-align: center;
			a {
				cursor: pointer; } } } }

footer {
	padding: 14px 23px 39px 23px;
	a {
		&:hover {
			text-decoration: none; } } }

.footer {
	&_divider {
		width: 299px;
		height: 4px;
		background-color: #1fbad6;
		margin: 0 auto; }
	&_wrapper {
		display: flex;
		justify-content: space-between;
		align-items: flex-start;
		padding-top: 32px; }
	&_wrapper > div {
		width: 33%;
		&:nth-child(3) {
			display: flex;
			justify-content: flex-end; } }
	&_social {
		width: 160px;
		display: flex;
		justify-content: space-between;
		&_item {
			display: flex;
			justify-content: center;
			align-items: center;
			margin: 0 4px;
			width: 32px;
			height: 32px;
			background-color: #070716;
			border-radius: 100%;
			i {
				color: #fff; } } }
	&_links {
		width: 390px;
		margin: 0 auto;
		&_main {
			display: flex;
			justify-content: space-between;
			padding: 0 10px;
			a {
				color: #070716;
				font-size: 15px;
				font-weight: 300;
				text-transform: uppercase; } }
		&_sub {
			display: flex;
			justify-content: space-between;
			margin-top: 10px;
			a {
				color: #999999;
				font-size: 11px;
				font-weight: 300;
				text-transform: uppercase; } }
		&_lang {
			display: block;
			margin: 0 auto;
			color: #1fbad6;
			font-size: 11px;
			font-weight: 400;
			margin-top: 19px;
			text-transform: uppercase;
			text-align: center; } } }

.fa-facebook-f,
.fa-twitter,
.fa-linkedin,
.fa-instagram {
	color: white; }
